@import "sass/index";

@keyframes buttonAnimate {
  0% {
    transform: scale(0.95);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes shadowAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.9;
  }
}

.container {
  position: relative;
  cursor: pointer;

  width: 100%;
  height: 100%;
  border-radius: 30px;
  overflow: hidden;

  background: linear-gradient(90deg, #000428 0%, #004e92 100%);

  @media (max-width: $mobileScreen) {
    border-radius: 10px;
  }
}

.name {
  position: absolute;
  top: 20%;
  left: 6%;
  width: 42% !important;
}
.button {
  width: 100%;
}
.buttonWrapper {
  position: absolute;
  bottom: 28%;
  left: 6%;
  width: 25% !important;
  animation: buttonAnimate 5s steps(55, end) infinite alternate;
}
.shadowAnimate {
  position: absolute;
  top: 1%;
  left: 1%;
  width: 98%;
  height: 98%;
  border-radius: 10px;

  box-shadow: 0px 0px 15px 5px #ffd700e6;
  animation: shadowAnimation 4s steps(25, end) infinite alternate;
}

.disable {
  & * {
    animation: none !important;
  }
}
