@import "sass/index";

@keyframes diceAnimate_1 {
  0% {
    transform: scale(0.9) rotate(-10deg);
  }

  100% {
    transform: scale(1) rotate(10deg);
  }
}

@keyframes diceAnimate_2 {
  0% {
    transform: scale(0.95) rotate(10deg);
  }

  100% {
    transform: scale(1) rotate(-10deg);
  }
}

@keyframes diceAnimate_3 {
  0% {
    transform: scale(0.9) rotate(10deg);
  }

  100% {
    transform: scale(1) rotate(-10deg);
  }
}

@keyframes diceAnimate_4 {
  0% {
    transform: scale(0.9) rotate(0deg);
  }

  100% {
    transform: scale(1) rotate(15deg);
  }
}

@keyframes shadowAnimate {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.9;
  }
}

$steps: steps(65, end);

.dice {
  position: absolute;

  &__1 {
    width: 13% !important;
    top: -18%;
    right: 44%;
    animation: diceAnimate_4 4s infinite alternate $steps;
  }

  &__5 {
    width: 13% !important;
    right: 23%;
    bottom: 1%;
    animation: diceAnimate_2 4s $steps infinite alternate;
  }
}

.withShadowWrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  & * {
    position: absolute;
    width: 100%;
  }

  &__2 {
    width: 19% !important;
    top: 24%;
    right: 16%;
    animation: diceAnimate_3 4s $steps infinite alternate;

    & > :first-child {
      animation: shadowAnimate 6s steps(30, end) infinite alternate;
    }
  }
  &__3 {
    width: 13% !important;
    top: 43%;
    right: 38%;
    animation: diceAnimate_1 4s $steps infinite alternate;

    & > :first-child {
      animation: shadowAnimate 4s steps(30, end) infinite alternate;
    }
  }
  &__4 {
    width: 20% !important;
    bottom: 20%;
    left: 37%;
    animation: diceAnimate_3 4s $steps infinite alternate;

    & > :first-child {
      animation: shadowAnimate 5s steps(50, end) infinite alternate;
    }
  }
  &__6 {
    width: 26% !important;
    right: -6.5%;
    bottom: 49%;
    animation: diceAnimate_1 4s $steps infinite alternate;

    & > :first-child {
      animation: shadowAnimate 7s steps(50, end) infinite alternate;
    }
  }
}
