@import "sass/index";

$mainColor: #01346e; // #01346e
$secondColor: #00123c; //#00123c

@keyframes rotateAnimate {
  0% {
    transform: rotate(-10deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

.root {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  animation: rotateAnimate 7s steps(80) infinite alternate;
}

.circle {
  background: $mainColor;
  position: absolute;
  border-radius: 50%;
  width: 16%;
  height: 45%;

  box-shadow: 0px 0px 15px 10px $mainColor;
}

.line {
  position: absolute;
  bottom: 45%;
  width: 14%;
  transform-origin: bottom center;
}
