@import "sass/index";

@keyframes coinAnimate_1 {
  0% {
    transform: rotate(-30deg);
  }

  100% {
    transform: rotate(20deg);
  }
}

@keyframes coinAnimate_2 {
  0% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(25deg);
  }
}

$steps: steps(50, end);

.coin {
  position: absolute;

  &__1 {
    width: 15% !important;
    top: 28%;
    left: -7%;

    animation: coinAnimate_1 6s steps(60, end) infinite alternate;
  }

  &__2 {
    width: 16% !important;
    top: -23%;
    right: 1%;

    animation: coinAnimate_1 5s steps(50, end) infinite alternate;
  }

  &__3 {
    width: 11% !important;
    bottom: -13%;
    right: 6%;

    animation: coinAnimate_2 10s steps(100, end) infinite alternate;
  }
}

@keyframes smallCoinAnimate_1 {
  0% {
    transform: translateY(-50%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes smallCoinAnimate_2 {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(50%);
  }
}

@keyframes smallCoinAnimate_3 {
  0% {
    transform: translateY(-20%);
  }

  100% {
    transform: translateY(20%);
  }
}

.smallCoin {
  position: absolute;

  &__1 {
    animation: smallCoinAnimate_1 5s steps(50, end) infinite alternate;

    width: 4.5% !important;
    top: 2%;
    left: 2%;
  }

  &__2 {
    width: 3.5% !important;
    left: 13%;
    top: -2%;

    animation: smallCoinAnimate_2 4s steps(50, end) infinite alternate;
  }

  &__3 {
    animation: smallCoinAnimate_1 5s steps(45, end) infinite alternate;

    width: 4% !important;
    left: 61%;
    top: 0%;
  }

  &__4 {
    animation: smallCoinAnimate_3 5s steps(65, end) infinite alternate;

    width: 6% !important;
    bottom: -10%;
    left: 21%;
  }

  &__5 {
    width: 4% !important;
    bottom: 14%;
    left: 34%;
    animation: smallCoinAnimate_3 3s steps(50, end) infinite alternate;
  }

  &__6 {
    width: 4% !important;
    bottom: 19%;
    left: 56%;

    animation: smallCoinAnimate_2 3.8s steps(40, end) infinite alternate;
  }

  &__7 {
    width: 3% !important;
    bottom: 20%;
    right: 18%;
    animation: smallCoinAnimate_3 4s steps(45, end) infinite alternate;
  }
}
